<template>
    <div class="layout-px-spacing">  
        <ul class="navbar-nav flex-row mt-2">
            <li>
                <div class="page-header">
                    <nav class="breadcrumb-one" aria-label="breadcrumb">
                        <ol class="breadcrumb">
                            <li class="breadcrumb-item"><a href="javascript:;">Masters</a></li>
                            <li class="breadcrumb-item active" aria-current="page"><span>Region</span></li>
                        </ol>
                    </nav>
                </div>
            </li>
        </ul>

        <div class="row">
            <div class="col-12">
                <h3 class="mb-3 text-center">Region Management</h3>
            </div>
        </div>

        <!-- loading spinner starts -->
        <div v-if="loading_spinner" v-html="tableShimmer()"></div>
        <!-- Loading spinner ends -->
        <div v-else class="row layout-top-spacing">
            <div class="col-xl-12 col-lg-12 col-sm-12 layout-spacing">
                <div class="panel p-0">
                    <div class="custom-table table3">
                        <v-client-table :data="region" :columns="regionColumns" :options="table_option">
                            <template #actions="props">
                                <a href="javascript:;" class="view me-3" @click="openViewModal(props.row)">
                                   <svg
                                        xmlns="http://www.w3.org/2000/svg"
                                        width="24"
                                        height="24"
                                        viewBox="0 0 24 24"
                                        fill="none"
                                        stroke="currentColor"
                                        stroke-width="1.5"
                                        stroke-linecap="round"
                                        stroke-linejoin="round"
                                        class="text-info"
                                    >
                                        <path d="M1 12s4-8 11-8 11 8 11 8-4 8-11 8-11-8-11-8z"></path>
                                        <circle cx="12" cy="12" r="3"></circle>
                                    </svg>
                                </a>
                            </template>
                        </v-client-table>
                    </div>
                </div>
            </div>
        </div>

        <!-- Region view modal starts -->
        <div class="modal fade mymodal" id="view-region-modal" data-bs-backdrop="false" data-bs-keyboard="false" tabindex="-1" aria-labelledby="viewRegionModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-fullscreen modal-dialog-scrollable">
                <div class="modal-content">
                    <div class="modal-header">
                        <h1 class="modal-title fs-5" id="viewRegionModalLabel"> {{ selectedRegion?.name || 'Region' }} Region </h1>
                        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close" @click="closeViewModal()"></button>
                    </div>
                    <div class="modal-body">
                        <div class="row">
                            <div :class="selectedDistrict && !selectedWard ? 'col-md-4' : selectedWard ? 'col-md-4' : 'col-md-12'">
                               
                                
                                <div class="d-flex me-4 mt-2 ">
                        <div class="ms-auto" >
                            <button class="btn btn-primary " @click="district_registration =! district_registration">                           
                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-plus-square" viewBox="0 0 16 16">
  <path d="M14 1a1 1 0 0 1 1 1v12a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1zM2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2z"/>
  <path d="M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4"/>
</svg>
</button>
                        </div>
                                </div> 

                                 <!-- district registration form starts -->
                     <form v-if="district_registration" class="mt-3 p-3 border rounded bg-white shadow" @submit.prevent="registerDistrict()">
                        <h4 class="text-center">DISTRICT REGISTRATION FORM</h4>
                        <div class="row mb-2">
                            <div class="col-md-6">
                                <label class="form-label">District Name</label>
                                <input type="text" class="form-control" placeholder="Enter District Name" v-model="district_name" required>
                            </div>
                            <div class="col-md-12">
                                <label class="form-label">Remark</label>
                                <input type="text" class="form-control" placeholder="Enter Remark" v-model="remark">
                            </div>
                        </div>
                        <div class="text-center gap-2 justify-content-end">
                            <button type="button" class="btn btn-danger me-3" @click="district_registration = false; resetDistrict();">Cancel</button>
                            <button type="submit" class="btn btn-primary" :disabled="loading_spinner">Register</button>
                        </div>
                     </form>
                     <!-- district registration form ends -->


                                <h4 class="mb-3 text-center">{{ selectedDistrict ? selectedDistrict.name + ' District' : 'District Lists' }}</h4>
                                <div class="row layout-top-spacing">
                                    <div class="col-xl-12 col-lg-12 col-sm-12 layout-spacing">
                                        <div class="panel p-0">
                                            <div class="custom-table table3">
                                                <v-client-table :data="districtList" :columns="districtColumns" :options="table_option">
                                                    <template #actions="props">
                                                        <div class="d-flex justify-content-center align-items-center">
                                                            <span v-if="props.row" class="text-success" @click="viewWard(props.row)" style="cursor: pointer;">
                                                                <svg v-if="selectedDistrict && props.row.id === selectedDistrict.id" xmlns="http://www.w3.org/2000/svg" width="26" height="26" fill="currentColor" class="bi bi-check2-circle" viewBox="0 0 16 16">
                                                                    <path d="M2.5 8a5.5 5.5 0 0 1 8.25-4.764.5.5 0 0 0 .5-.866A6.5 6.5 0 1 0 14.5 8a.5.5 0 0 0-1 0 5.5 5.5 0 1 1-11 0"/>
                                                                    <path d="M15.354 3.354a.5.5 0 0 0-.708-.708L8 9.293 5.354 6.646a.5.5 0 1 0-.708.708l3 3a.5.5 0 0 0 .708 0z"/>
                                                                </svg>
                                                                <svg v-else xmlns="http://www.w3.org/2000/svg" width="26" height="26" fill="currentColor" class="bi bi-circle" viewBox="0 0 16 16">
                                                                    <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14m0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16"/>
                                                                </svg>
                                                            </span>
                                                        </div>
                                                    </template>
                                                </v-client-table>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="col-md-4" v-if="selectedDistrict"> 

                                <div class="d-flex me-4 mt-2 ">
                        <div class="ms-auto" >
                            <button class="btn btn-primary " @click="ward_registration =! ward_registration">                           
                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-plus-square" viewBox="0 0 16 16">
  <path d="M14 1a1 1 0 0 1 1 1v12a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1zM2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2z"/>
  <path d="M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4"/>
</svg>
</button>
                        </div>
                                 </div>
                             
                                  <!-- ward registration form starts -->
                     <form v-if="ward_registration" class="mt-3 p-3 border rounded bg-white shadow" @submit.prevent="registerWard()">
                        <h4 class="text-center">WARD REGISTRATION FORM</h4>
                        <div class="row mb-2">
                            <div class="col-md-6">
                                <label class="form-label">Ward Name</label>
                                <input type="text" class="form-control" placeholder="Enter Ward Name" v-model="ward_name" required>
                            </div>
                            <div class="col-md-12">
                                <label class="form-label">Remark</label>
                                <input type="text" class="form-control" placeholder="Enter Remark" v-model="ward_remark">
                            </div>
                        </div>
                        <div class="text-center gap-2 justify-content-end">
                            <button type="button" class="btn btn-danger me-3" @click="ward_registration = false; resetWard();">Cancel</button>
                            <button type="submit" class="btn btn-primary" :disabled="loading_spinner">Register</button>
                        </div>
                     </form>
                     <!-- ward registration form ends -->

                                <h4 class="mb-3 text-center">{{ selectedWard ? selectedWard.name + 'Ward' : 'Ward Lists' }}</h4>
                                <div class="row layout-top-spacing">
                                    <div class="col-xl-12 col-lg-12 col-sm-12 layout-spacing">
                                        <div class="panel p-0">
                                            <div class="custom-table table3">
                                                <v-client-table :data="wardList" :columns="wardColumns" :options="table_option">    
                                                    <template #actions="props">
                                                        <div class="d-flex justify-content-center align-items-center">
                                                            <span v-if="props.row" class="text-success" @click="viewStreet(props.row)" style="cursor: pointer;">
                                                                <svg v-if="selectedWard && props.row.id === selectedWard.id" xmlns="http://www.w3.org/2000/svg" width="26" height="26" fill="currentColor" class="bi bi-check2-circle" viewBox="0 0 16 16">
                                                                    <path d="M2.5 8a5.5 5.5 0 0 1 8.25-4.764.5.5 0 0 0 .5-.866A6.5 6.5 0 1 0 14.5 8a.5.5 0 0 0-1 0 5.5 5.5 0 1 1-11 0"/>
                                                                    <path d="M15.354 3.354a.5.5 0 0 0-.708-.708L8 9.293 5.354 6.646a.5.5 0 1 0-.708.708l3 3a.5.5 0 0 0 .708 0z"/>
                                                                </svg>
                                                                <svg v-else xmlns="http://www.w3.org/2000/svg" width="26" height="26" fill="currentColor" class="bi bi-circle" viewBox="0 0 16 16">
                                                                    <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14m0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16"/>
                                                                </svg>
                                                            </span>
                                                        </div>
                                                    </template>    
                                                </v-client-table>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="col-md-4" v-if="selectedWard">

                                <div class="d-flex me-4 mt-2 ">
                        <div class="ms-auto" >
                            <button class="btn btn-primary " @click="street_registration =! street_registration">                           
                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-plus-square" viewBox="0 0 16 16">
  <path d="M14 1a1 1 0 0 1 1 1v12a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1zM2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2z"/>
  <path d="M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4"/>
</svg>
</button>
                        </div>
                                </div>

                                 <!-- street registration form starts -->
                     <form v-if="street_registration" class="mt-3 p-3 border rounded bg-white shadow" @submit.prevent="registerStreet()">
                        <h4 class="text-center">STREET REGISTRATION FORM</h4>
                        <div class="row mb-2">
                            <div class="col-md-6">
                                <label class="form-label">Street Name</label>
                                <input type="text" class="form-control" placeholder="Enter Street Name" v-model="street_name" required>
                            </div>
                            <div class="col-md-12">
                                <label class="form-label">Remark</label>
                                <input type="text" class="form-control" placeholder="Enter Remark" v-model="street_remark">
                            </div>
                        </div>
                        <div class="text-center gap-2 justify-content-end">
                            <button type="button" class="btn btn-danger me-3" @click="street_registration = false; resetStreet();">Cancel</button>
                            <button type="submit" class="btn btn-primary" :disabled="loading_spinner">Register</button>
                        </div>
                     </form>
                     <!-- street registration form ends -->


                                <h4 class="mb-3 text-center"> Street Lists</h4>
                                <div class="row layout-top-spacing">
                                    <div class="col-xl-12 col-lg-12 col-sm-12 layout-spacing">
                                        <div class="panel p-0">
                                            <div class="custom-table table3">
                                                <v-client-table :data="streetList" :columns="streetColumns" :options="table_option">    
                                                    <template #actions="props">
                                                        <div class="d-flex justify-content-center align-items-center">
                                                            <!-- You can add actions here if needed -->
                                                        </div>
                                                    </template>    
                                                </v-client-table>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" @click="closeViewModal()" id="close_header">Close</button>
                    </div>
                </div>
            </div>
        </div>
        <!-- Region view modal ends -->
    </div>
</template>

<style>
    .table3 .actions svg {
        padding: 2px;
        cursor: pointer;
    }
</style>

<script setup>
    import { onMounted, ref, inject } from 'vue';
    import axios from 'axios';
    import { useMeta } from '@/composables/use-meta';
     import Multiselect from '@suadelabs/vue3-multiselect';
    import '@suadelabs/vue3-multiselect/dist/vue3-multiselect.css';
    
    useMeta({ title: 'Region Master' });  
    
    const showMessage = inject('showMessage');
    const showError = inject('showError');
    const user_permissions = JSON.parse(localStorage.getItem("user")).permissions;
    const formatDate = inject('formatDate');
    const tableShimmer = inject('tableShimmer');
    const loadingSpinner = inject('loadingSpinner'); 
    
    const loading_spinner = ref(false);
    const regionColumns = ref(['SNo', 'name','remark', 'actions']);
    const districtColumns = ref(['SNo', 'name', 'remark', 'actions']);
    const wardColumns = ref(['SNo', 'name', 'remark', 'actions']);
    const streetColumns = ref(['SNo', 'name', 'remark']);
    const region = ref([]);
    const districtList = ref([]);
    const wardList = ref([]);
    const streetList = ref([]);
    const selectedRegion = ref(null);
    const selectedDistrict = ref(null);
    const selectedWard = ref(null);
    const selectedStreet = ref(null);
    const district_registration = ref(false);
    const ward_registration = ref(false);
    const street_registration = ref(false);

    const table_option = ref({
        perPage: 20,
        perPageValues: [20, 50, 100],
        skin: 'table table-hover table-striped',
        columnsClasses: { actions: 'actions text-center' },
        sortable: ['SNo', 'name'],
        sortIcon: {
            base: 'sort-icon-none',
            up: 'sort-icon-asc',
            down: 'sort-icon-desc',
        },
        pagination: { nav: 'scroll', chunk: 5 },
        texts: {
            count: 'Showing {from} to {to} of {count}',
            filter: '',
            filterPlaceholder: 'Search...',
            limit: 'Results:',
        },
        resizableColumns: false,
    });

    const axiosInstance = axios.create({
        baseURL: process.env.VUE_APP_API_BASE_URL
    });

    const getRegion = async () => {
        loading_spinner.value = true;
        try {
            const response = await axiosInstance.get('regions');
            region.value = response.data.map((item, index) => ({
                ...item,
                SNo: index + 1,
                id: item.id,
                name: item.name,
                remark: item.remark,
            }));
        } catch (error) {
            console.log(error);
            showError(error.message);
        } finally {
            loading_spinner.value = false;
        }
    };

    const getDistrict = async (regionId) => {
        try {
            const response = await axiosInstance.get(`/districts?region_id=${regionId}`);
            districtList.value = response.data.map((item, index) => ({
                ...item,
                SNo: index + 1,
            }));
        } catch (error) {
            showError(error.message);
        }
    };

    const getWard = async (districtId) => {
        try {
            const response = await axiosInstance.get(`/wards?district_id=${districtId}`);
            wardList.value = response.data.map((item, index) => ({
                ...item,
                SNo: index + 1,
            }));
        } catch (error) {
            showError(error.message);
        }
    };

    const getStreet = async (wardId) => {
        try {
            const response = await axiosInstance.get(`/streets?ward_id=${wardId}`);
            streetList.value = response.data.map((item, index) => ({
                ...item,
                SNo: index + 1,
            }));
        } catch (error) {
            showError(error.message);
        }
    };

   

    const openViewModal = async (row) => {
        selectedRegion.value = row;
        selectedDistrict.value = null;
        selectedWard.value = null;
        wardList.value = [];
        streetList.value = [];
        
        try {
            await getDistrict(row.id);
            
            // Check if no districts found
            if (districtList.value.length === 0) {
                showError(`No districts found for "${row.name}"`);
                return;
            }
            
            // Only open modal if districts are found
            const modalEl = document.getElementById('view-region-modal');
            if (modalEl) {
                const modal = new bootstrap.Modal(modalEl);
                modal.show();
            }
        } catch (error) {
            showError(error.message);
        }
    };

    const viewWard = async (row) => {
        selectedDistrict.value = row;
        selectedWard.value = null;
        streetList.value = [];
        await getWard(row.id);
    };

    const viewStreet = async (row) => {
        selectedWard.value = row;
        await getStreet(row.id);
    };

    const closeViewModal = () => {
        selectedRegion.value = null;
        selectedDistrict.value = null;
        selectedWard.value = null;
        districtList.value = [];
        wardList.value = [];
        streetList.value = [];
    };


    // District Registration
    const district_name = ref('');
    const district_region = ref(null);
    const remark = ref('');
    const regionList = ref([]);

    const registerDistrict = async () => {
        loading_spinner.value = true;
        try {
            const payload = {
                name: district_name.value,
                region_id: selectedRegion.value.id, // Use already selected region
                remark: remark.value
            };
            await axiosInstance.post('/districts', payload);
            showMessage('District registered successfully');
            district_registration.value = false;
            resetDistrict();
            if (selectedRegion.value) {
                await getDistrict(selectedRegion.value.id);
            }
        } catch (error) {
            showError(error.message);
        } finally {
            loading_spinner.value = false;
        }
    };

    const resetDistrict = () => {
        district_name.value = '';
        district_region.value = null;
        remark.value = '';
    };

    // Ward Registration
    const ward_name = ref('');
    const ward_district = ref(null);
    const ward_remark = ref('');

    const registerWard = async () => {
        loading_spinner.value = true;
        try {
            const payload = {
                name: ward_name.value,
                district_id: selectedDistrict.value.id, // Use already selected district
                remark: ward_remark.value
            };
            await axiosInstance.post('/wards', payload);
            showMessage('Ward registered successfully');
            ward_registration.value = false;
            resetWard();
            if (selectedDistrict.value) {
                await getWard(selectedDistrict.value.id);
            }
        } catch (error) {
            showError(error.message);
        } finally {
            loading_spinner.value = false;
        }
    };

    const resetWard = () => {
        ward_name.value = '';
        ward_district.value = null;
        ward_remark.value = '';
    };

    // Street Registration
    const street_name = ref('');
    const street_ward = ref(null);
    const street_remark = ref('');

    const registerStreet = async () => {
        loading_spinner.value = true;
        try {
            const payload = {
                name: street_name.value,
                ward_id: selectedWard.value.id, // Use already selected ward
                remark: street_remark.value
            };
            await axiosInstance.post('/streets', payload);
            showMessage('Street registered successfully');
            street_registration.value = false;
            resetStreet();
            if (selectedWard.value) {
                await getStreet(selectedWard.value.id);
            }
        } catch (error) {
            showError(error.message);
        } finally {
            loading_spinner.value = false;
        }
    };

    const resetStreet = () => {
        street_name.value = '';
        street_ward.value = null;
        street_remark.value = '';
    };

    // Load region list for district registration
    const loadRegionList = async () => {
        try {
            const response = await axiosInstance.get('regions');
            regionList.value = response.data;
        } catch (error) {
            showError(error.message);
        }
    };

    onMounted(() => {
        getRegion();  
        loadRegionList();       // Load region list for district registration form
    });
</script>
